.hacia-arriba{
	position: relative;
	bottom: 70%;
}

.instrucciones-fondo{
	background-color: rgba(255, 255, 255, 0.90);
	grid-row: 1 / span 4;
	grid-column: 1 / span 5;
	z-index: 5;
	width: 100%;
}

.instrucciones-contenedor{
	background-color: white;
	margin-top: 8%;
	margin-left: 25%;
	z-index: 6;
	width: 50%;
}

.borde-azul{
	border: 4px #2c72ff solid;

}

.borde-perdiste{
	border: 4px #f84045 solid;
}

.boton{
	cursor: pointer;
}

.boton:hover{
	-webkit-box-shadow: 0px 1px 8px 5px rgba(0,0,0,0.20);
	box-shadow: 0px 1px 8px 5px rgba(0,0,0,0.20);
}

.sombreado{
	-webkit-box-shadow: 0px 1px 8px 5px rgba(0,0,0,0.40);
	box-shadow: 0px 1px 8px 5px rgba(0,0,0,0.40);
}

.block{
	display:block;
}


/*GRIDS*/

.grid{
	display: grid;
	grid-template: 23% 23% 23% 23% 8% / 25% 25% 25% 25%;
	
}

.super-grid {
	width: 100%;
	display: grid;
	/*grid-template:  5px 50px 405px 100px / 18% 30% 2% 30% 18%;*/
	grid-template:  10% 30px 80% 10% / 18% 30% 2% 30% 18%;
	justify-items: center;
	
}

.cuadricula{
	grid-row: 3 / span 1;
	grid-column: 2 / span 1;
	height: 100%;
}

.intentos{
	grid-row: 3 / span 1;
	grid-column: 4 / span 1;
}


/* SCREEN SIZES */

@media screen and (max-width:768px) {
   .grid{
	display: grid;
	grid-template: 75px 75px 75px 75px / 75px 75px 75px 75px;
	justify-items: center;
	}

	.super-grid {
		width: 100%;
		display: grid;
		/*grid-template:  5px 50px 405px 100px / 18% 30% 2% 30% 18%;*/
		grid-template:  30px 70% 30px 30% / 10% 80% 10%;
		justify-items: center;

	}

	.cuadricula{
		grid-row: 2 / span 1;
		grid-column: 2 / span 1;
		height: 100%;
	}
	
	.intentos{
		grid-row: 4 / span 1;
		grid-column: 2 / span 1;
		width: 75%;
	} 
}

@media screen and (max-width:480px) {
   .grid{
	display: grid;
	grid-template: 50px 50px 50px 50px / 50px 50px 50px 50px;
	justify-items: center;
	}

	.super-grid {
		width: 100%;
		display: grid;
		/*grid-template:  5px 50px 405px 100px / 18% 30% 2% 30% 18%;*/
		grid-template:  20px 70% 20px 30% / 10% 80% 10%;
		justify-items: center;

	}

	.cuadricula{
		grid-row: 2 / span 1;
		grid-column: 2 / span 1;
		height: 100%;
	}
	
	.intentos{
		grid-row: 4 / span 1;
		grid-column: 2 / span 1;
		width: 75%;
	} 
}


